import React, { useEffect, useState } from 'react';
import { Socket } from 'socket.io-client'
import { DefaultEventsMap } from 'socket.io/dist/typed-events';

interface PlayerToken {
    token: string,
    socket: Socket
    [key: string]: any
}

interface PlayOptions {
    spotify_uri: string,
    id: string,
    playerInstance: Spotify.Player
}

interface FadeOptions {
    direction: 'in' | 'out',
    player: Spotify.Player,
    duration: number
}

const Player = (props: PlayerToken) => {
    const track = {
        name: "",
        album: {
            images: [
                { url: "" }
            ]
        },
        artists: [
            { name: "" }
        ]
    }

    const play = (args: PlayOptions) => {
        return new Promise<void>((resolve, reject) => {
            args.playerInstance._options.getOAuthToken(access_token => {
                fetch(`https://api.spotify.com/v1/me/player/play?device_id=${args.id}`, {
                    method: 'PUT',
                    body: JSON.stringify({ context_uri: args.spotify_uri }),
                    headers: {
                        'Content-Type': 'application/json',
                        'Authorization': `Bearer ${access_token}`
                    },
                }).then(() => {
                    resolve();
                })
            });
        })
    };

    const fade = (args: FadeOptions) => {
        return new Promise<void>((resolve, reject) => {
            let interval: number;
            let val = (1 / ((args.duration) / 5))

            if (args.direction === 'in') {
                interval = window.setInterval(async () => {
                    args.player.setVolume(Math.min(1, await args.player.getVolume() + val))
                }, 5)
            } else {
                interval = window.setInterval(async () => {
                    let vol = await args.player.getVolume();
                    args.player.setVolume(Math.max(0, vol - val))
                }, 5)
            }

            window.setTimeout(() => {
                clearInterval(interval)
                resolve();
            }, args.duration)
        })
    }

    const [player, setPlayer] = useState<Spotify.Player | undefined>(undefined);
    const [device_id, setDevice_Id] = useState<string>('');
    const [is_paused, setPaused] = useState(false);
    const [is_active, setActive] = useState(false);
    const [current_track, setTrack] = useState(track);
    const [duration, setDuration] = useState(0);
    const [currentTime, setTime] = useState(0);
    const [fadeDuration, setFadeDuration] = useState(2000);
    const [popupActive, setPopupActive] = useState(true);

    useEffect(() => {
        const script = document.createElement("script");
        script.src = "https://sdk.scdn.co/spotify-player.js";
        script.async = true;

        document.body.appendChild(script);

        window.onSpotifyWebPlaybackSDKReady = () => {

            const player = new window.Spotify.Player({
                name: 'Web Playback SDK',
                getOAuthToken: cb => {
                    cb(props.token);
                },
                volume: 1
            });

            setPlayer(player);

            player.addListener('ready', ({ device_id }) => {
                setDevice_Id(device_id)
                console.log('Ready with Device ID', device_id, device_id);
            });

            player.addListener('not_ready', ({ device_id }) => {
                console.log('Device ID has gone offline', device_id);
            });

            player.addListener('playback_error', (err) => {
                console.log(err);
            })

            player.addListener('player_state_changed', (state => {

                if (!state) return

                setTime(Math.floor(state.position / 1000))
                setDuration(state.duration)
                setTrack(state.track_window.current_track);
                setPaused(state.paused);

                player.getCurrentState().then(state => {
                    (!state) ? setActive(false) : setActive(true)
                });
            }));

            player.connect();
        };
    }, []);

    useEffect(() => {
        const socket = props.socket;
        if (player === undefined) return;
        if (device_id === undefined || device_id === '') return;

        socket.on('play', (data) => {
            console.log('GOT PLAY');

            if (is_paused === true) {
                player.togglePlay()
                console.log('Toggled Pla');
            }
            console.log('PLAY');
            fade({
                direction: 'out',
                duration: fadeDuration,
                player: player,
            }).then(() => {
                play({
                    playerInstance: player,
                    id: device_id,
                    spotify_uri: data
                }).then(() => {
                    fade({
                        direction: 'in',
                        duration: fadeDuration,
                        player: player,
                    })
                })
            })
        })

        socket.on('pause', () => {
            if (is_paused === true) player.togglePlay();
        })

        socket.on('crossfade', () => {
        })

        return () => {
            socket.offAny();
        }
    }, [player, device_id])

    useEffect(() => {
        let interval: number;

        if (is_paused === false) {
            interval = window.setInterval(() => {
                setTime(currentTime => currentTime + 1)
            }, 1000)
        }

        return () => {
            clearInterval(interval)
        }
    }, [currentTime])

    return (
        <>
            {
                player !== undefined ?
                    <>
                        {
                            popupActive ?
                                <div className="popup">
                                    <button 
                                    className="button primaryButton"
                                    onClick={()=>{
                                        setPopupActive(false);
                                    }}
                                    >
                                        Bitte einmal dr√ºcken</button>
                                </div>
                                :
                                <>
                                </>
                        }

                        <div className="container">
                            <div className="main-wrapper">
                                <img src={current_track.album.images[0].url}
                                    className="now-playing__cover" alt="" />
                                <div className="now-playing__side">
                                    <div className="now-playing__name">{
                                        current_track.name
                                    }</div>
                                    <div>
                                    </div>
                                    <div className="now-playing__artist">{
                                        current_track.artists[0].name
                                    }</div>
                                    <div>{currentTime}</div>
                                    <div>{`${Math.floor((duration / 1000) / 60)}:${Math.floor((duration / 1000) % 60)}`}</div>
                                </div>
                            </div>
                        </div>
                        <button className="button btn-spotify" onClick={() => { player.previousTrack() }} >
                            &lt;&lt;
                        </button>

                        <button className="button btn-spotify" onClick={() => {
                            player.togglePlay()
                        }}
                        >
                            {is_paused ? "PLAY" : "PAUSE"}
                        </button>

                        <button className="button btn-spotify" onClick={() => { player.nextTrack() }} >
                            &gt;&gt;
                        </button>
                    </>
                    :
                    <></>
            }

        </>
    )
}

export { Player }