import React, { useEffect, useState } from 'react';
import logo from './logo.svg';
import './App.css';
import { io } from 'socket.io-client';
import { Player } from './Player';

function App() {
  const socket = io('http://localhost:3500');
  const [token, setToken] = useState('');


  useEffect(() => {
    socket.on('initalToken', (data) => {
      setToken(data)

      setInterval(()=>{
        socket.emit('requestNewToken')
      }, 3600 * 1000)
    })

    socket.on('recievedToken', (data)=>{
      setToken(data)
    })
  }, [])

  return (
    <div className="App">
        {
          token !== '' ? <Player token={token} socket={socket} /> : <></>
        }
    </div>
  );
}

export default App;
